  padding: 10px 20px;
  color: white;
  font-size: 14px;
  margin-top: 10px;
  margin-bottom: 20px;
}

.button-pink:hover {
  background-color: #ed768d;
}

.divider {
  width: 10%;
  margin-top: -10px;
